// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Utils
import { isUserLoggedIn } from '@utils'

// ** Store & Actions
import { useDispatch } from 'react-redux'
import { handleLogout } from '@store/actions/auth'

// ** Third Party Components
import { UncontrolledDropdown, DropdownMenu, DropdownToggle, DropdownItem } from 'reactstrap'
import { User, Mail, CheckSquare, MessageSquare, Settings, CreditCard, HelpCircle, Power, ChevronDown, ChevronsDown, Bell } from 'react-feather'

// ** Default Avatar Image
import defaultAvatar from '@src/assets/images/portrait/small/avatar-s-11.jpg'

const UserDropdown = () => {
  // ** Store Vars
  const dispatch = useDispatch()

  // ** State
  const [userData, setUserData] = useState(null)

  //** ComponentDidMount
  useEffect(() => {
    if (isUserLoggedIn() !== null) {
      setUserData(JSON.parse(localStorage.getItem('userData')))
    }
  }, [])

  //** Vars
  const userAvatar = (userData && userData.avatar) || defaultAvatar

  return (
    <Fragment>
      <Power className='mx-1' />
      <MessageSquare className='mx-1'/>
      <Bell  className='ml-1 mr-5'/>
      <UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
        <DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
          <Avatar img={userAvatar} imgHeight='40' imgWidth='40' />
          <div className=''>
            <span className='user-name font-weight-bold'>Hello, {(userData && userData['username']) || 'Rohit Sharma'}</span>
            <span>
              <ChevronDown />
            </span>
          </div>
        </DropdownToggle>
        <DropdownMenu right>
          <DropdownItem tag={Link} to='#' onClick={e => e.preventDefault()}>
            <User size={14} className='mr-75' />
            <span className='align-middle'>Profile</span>
          </DropdownItem>
          <DropdownItem tag={Link} to='#' onClick={e => e.preventDefault()}>
            <Mail size={14} className='mr-75' />
            <span className='align-middle'>Inbox</span>
          </DropdownItem>
          <DropdownItem tag={Link} to='#' onClick={e => e.preventDefault()}>
            <CheckSquare size={14} className='mr-75' />
            <span className='align-middle'>Tasks</span>
          </DropdownItem>
          <DropdownItem tag={Link} to='#' onClick={e => e.preventDefault()}>
            <MessageSquare size={14} className='mr-75' />
            <span className='align-middle'>Chats</span>
          </DropdownItem>
          <DropdownItem tag={Link} to='/login' onClick={() => dispatch(handleLogout())}>
            <Power size={14} className='mr-75' />
            <span className='align-middle'>Logout</span>
          </DropdownItem>
        </DropdownMenu>
      </UncontrolledDropdown>
    </Fragment>
  )
}

export default UserDropdown
